// src/components/Stats.jsx
import React from "react";

function groupByDate(records = []) {
  const map = new Map();
  for (const r of records) {
    const d = r.date || "";
    if (!map.has(d)) map.set(d, []);
    map.get(d).push(Number(r.score) || 0);
  }
  const rows = [];
  for (const [date, arr] of map.entries()) {
    const avg = arr.reduce((a, b) => a + b, 0) / Math.max(1, arr.length);
    rows.push({ date, avg });
  }
  return rows;
}

export default function Stats({ records = [], maxY = 40, expectationLine = null }) {
  const rows = groupByDate(records);
  const points = rows.map(r => ({ x: r.date, y: Number(r.avg) || 0 }));

  const pointCount = Math.max(1, points.length);
  const widthPerPoint = 80;
  const svgWidth = Math.max(600, pointCount * widthPerPoint);
  const svgHeight = 220;
  const padding = { top: 16, right: 40, bottom: 40, left: 40 };

  const innerW = svgWidth - padding.left - padding.right;
  const innerH = svgHeight - padding.top - padding.bottom;

  const yScale = (v) => {
    const clamped = Math.max(0, Math.min(maxY, v));
    return padding.top + (1 - clamped / maxY) * innerH;
  };

  const xScale = (i) => {
    if (pointCount === 1) return padding.left + innerW / 2;
    return padding.left + (i / (pointCount - 1)) * innerW;
  };

  const pathD = points.map((p, i) => {
    const x = xScale(i);
    const y = yScale(p.y);
    return `${i === 0 ? "M" : "L"} ${x} ${y}`;
  }).join(" ");

  const yTicks = [];
  for (let t = 0; t <= maxY; t += 10) yTicks.push(t);

  return (
    <div style={{ width: "100%", overflowX: "auto" }}>
      <svg width={svgWidth} height={svgHeight}>
        <rect x="0" y="0" width={svgWidth} height={svgHeight} fill="transparent" />

        {/* y grid & labels */}
        {yTicks.map((tick) => {
          const y = yScale(tick);
          return (
            <g key={tick}>
              <line x1={padding.left} x2={svgWidth - padding.right} y1={y} y2={y} stroke="#eef5fb" strokeWidth={1} />
              <text x={8} y={y + 4} fontSize={12} fill="#6b7b87">{tick}</text>
            </g>
          );
        })}

        {/* expectation line (red dashed) */}
        {typeof expectationLine === "number" && expectationLine >= 0 && expectationLine <= maxY && (
          <g>
            <line
              x1={padding.left}
              x2={svgWidth - padding.right}
              y1={yScale(expectationLine)}
              y2={yScale(expectationLine)}
              stroke="#d94b4b"
              strokeWidth={2}
              strokeDasharray="6,6"
            />
            <text x={svgWidth - padding.right - 4} y={yScale(expectationLine) - 6} fontSize={12} fill="#d94b4b" textAnchor="end">
               {expectationLine}
            </text>
          </g>
        )}

        {/* x labels */}
        {points.map((p, i) => {
          const x = xScale(i);
          const y = svgHeight - 8;
          return (
            <text key={i} x={x} y={y} fontSize={12} fill="#6b7b87" textAnchor="middle" transform={`rotate(-25 ${x},${y})`}>
              {p.x}
            </text>
          );
        })}

        {/* line */}
        <path d={pathD} fill="none" stroke="#1f6ef8" strokeWidth={3} strokeLinecap="round" strokeLinejoin="round" />

        {/* points */}
        {points.map((p, i) => {
          const x = xScale(i);
          const y = yScale(p.y);
          return <circle key={i} cx={x} cy={y} r={5} fill="#fff" stroke="#1f6ef8" strokeWidth={2} />;
        })}
      </svg>
    </div>
  );
}
